.calendar .day {
  position: relative;
  width: calc(100% / 7);
  height: 44px;
  display: inline-block;
  background-color: white;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  z-index: 1;
  text-align: center;
}
.selected {
  height: 40px;
  width: 40px;
  border: 5px solid rgba(37, 37, 158, 0.6);
  background-color: red;
  margin-bottom: 5px;
}
.week {
  position: relative;
  width: calc(100% / 7);
  height: 44px;
  display: inline-block;
  background-color: white;
}
.header {
  display: flex;
  justify-content: space-between;
  background-color: rgb(179, 159, 159);
  padding: 5px;
  margin: 10px;
}
